import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/Layout"
import AddToCartButton from "../components/AddtoCartButton"
import { useStockQuantity } from "../functions/useStockQuantity"
import AddToSnipCart from "../components/AddToSnipCart"

export default function SingleProductPage({ data: { product } }) {
  const { stockQuantity, fetching } = useStockQuantity(product.wooCommerceId)

  return (
    <Layout>
      <h1>
        {product.name}
        {/* Ez egy példa, hogy lehet megjeleníteni, ha pl akciós a termék */}
        {product.on_sale && <span>SALE!</span>}
      </h1>
      <div>
        {fetching ? <p>Fetching stock...</p> : <p>{stockQuantity} in stock</p>}
      </div>
      <div dangerouslySetInnerHTML={{ __html: product.price_html }} />
      {/* <AddToCartButton product={product} /> */}
      {fetching ? null : <AddToSnipCart product={product} />}

      {product.images &&
        product.images.map((image, i) => (
          <img key={i} src={image.src} alt={image.name} />
        ))}
    </Layout>
  )
}

// This needs to be dynamic based on the slug passed in via context in gatsby-node.js
export const query = graphql`
  query($id: String!) {
    product: wcProduct(id: { eq: $id }) {
      name
      id
      wooCommerceId
      slug
      on_sale
      stock_quantity
      images {
        name
        src
      }
      price_html
      price
      regular_price
      sale_price
    }
  }
`
