import { useState, useEffect } from "react"

export function useStockQuantity(productId) {
  const [stockQuantity, setStockQuantity] = useState(null)
  const [fetching, setFetching] = useState(true)

  useEffect(() => {
    const auth = btoa(
      `${process.env.GATSBY_WOO_CONSUMER_KEY}:${process.env.GATSBY_WOO_CONSUMER_SECRET}`
    )
    fetch(
      `${process.env.GATSBY_WP_BASE_URL}/wp-json/wc/v3/products/${productId}`,
      {
        method: "GET",
        headers: { Authorization: `Basic ${auth}` },
      }
    )
      .then(res => res.json())
      .then(json => {
        if (json.stock_quantity === null) {
          setStockQuantity(0)
        } else {
          setStockQuantity(json.stock_quantity)
        }
        setFetching(false)
      })
      .catch(err => {
        console.error(err)
        setFetching(false)
      })
  }, [productId])

  return { stockQuantity, fetching }
}
